<template>
  <div style="margin:20px">
    <Steps :current="step">
      <Step title="申请微信支付" content="通过微信小程序后台申请开通"></Step>
      <Step title="微信支付参数配置" content="填写微信支付商户号和API密钥"></Step>
      <Step title="完成配置" content="完成配置后即可使用微信支付"></Step>
    </Steps>
    <div style="margin-top: 60px;"></div>
    <Form ref="formValidate" :model="wxapayconfig" :rules="ruleValidate" label-position="left" :label-width="180">
      <FormItem label="微信支付商户号" prop="mchid">
        <Input v-model="wxapayconfig.mchid" placeholder="请输入微信支付商户号！" size="large"></Input>
      </FormItem>
      <FormItem label="API 密钥" prop="signkey">
        <Input v-model="wxapayconfig.signkey" placeholder="请输入API 密钥！" size="large"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" size="large" style="width:120px;">
          <span v-if="!loading">{{step==1?'提交配置':'保存修改'}}</span>
          <span v-else>正在保存..</span>
        </Button>
        <Button type="ghost" @click="handleReset('formValidate')" size="large" style="margin-left: 8px;width:80px">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      step:1,
      loading: false,
      wxapayconfig: {
        mchid: '',
        signkey: '',
      },
      ruleValidate: {
        mchid: [
          { required: true, message: '请输入微信支付商户号！', trigger: 'blur' }
        ],
        signkey: [
          { required: true, message: '请输入API 密钥！', trigger: 'blur' }
        ],
      }
    }
  },
  mounted: function() {
    this.$sitemap.prvname = "小程序管理";
    this.$sitemap.currname = '微信支付配置';
    this.$menuactive.open = ['SystemBlock'];
    this.$menuactive.active = 'WxaPayConfig';
    this.getPayconfigDetail()
  },
  methods: {
    handleSubmit(name) {
      var thatform = this.$refs[name];
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.postPayconfigSave(thatform);
        } else {
          this.$Message.error('还没有完整的信息输入！');
        }
      })
    },
    handleReset(name) {
      this.$refs[name].resetFields();
    },
    getPayconfigDetail() {
      this.$http.post(this.$api.payconfig_detail,{}).then(res=>{
        console.log('微信支付配置信息',res.data)
        if(res.data.data) {
          this.wxapayconfig.mchid = res.data.data.mchid;
          this.wxapayconfig.signkey = res.data.data.signkey;
          this.step = 2;
        }else {
          this.step = 1;
        }
      }).catch(error=>{
        console.log('微信配置detail失败',error)
      })
    },
    postPayconfigSave(thatform) {
      var that = this;
      this.$http.post('/payconfig/save', {mchid:this.wxapayconfig.mchid,signkey:this.wxapayconfig.signkey}).then((res)=> {
        if (res.data.result == 1) {
          thatform.resetFields();
          that.$Message.success('小程序参数配置成功!');
        } else {
          that.$Message.error('操作失败，请检查原因！');
        }
      }).catch(error=>{
        console.log(error)
        that.$Message.error('操作失败！'+error)
      }).then(()=>{
        this.loading = false
      });
    }
  }
}

</script>



